.app {
  max-width: 100%;
  margin: 40px 135px;
  text-decoration: none;
  scroll-behavior: smooth;
}
.logo-wrapper {
  margin-bottom: 39px;
}
.app-header {
  display: flex;
  justify-content: space-between;
}
.header-login {
  display: flex;
  align-items: center;
}
.main-block-wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-gap: 5px;
  margin-top: 25px;
}
.home-block-wrapper h1 {
  margin-bottom: 70px;
  font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
  font-size: 65px;
  text-align: center;
  text-transform: uppercase;
  text-rendering: optimizeLegibility;
  color: #393939;
  letter-spacing: 0.15em;
  text-shadow: 1px -1px 0 #767676, -1px 2px 1px #737272, -2px 4px 1px #767474, -3px 6px 1px #787777,
    -4px 8px 1px #7b7a7a, -5px 10px 1px #7f7d7d, -6px 12px 1px #828181, -7px 14px 1px #868585, -8px 16px 1px #8b8a89,
    -9px 18px 1px #8f8e8d, -10px 20px 1px #949392, -11px 22px 1px #999897, -12px 24px 1px #9e9c9c,
    -13px 26px 1px #a3a1a1, -14px 28px 1px #a8a6a6, -15px 30px 1px #adabab, -16px 32px 1px #b2b1b0,
    -17px 34px 1px #b7b6b5, -18px 36px 1px #bcbbba, -19px 38px 1px #c1bfbf, -20px 40px 1px #c6c4c4,
    -21px 42px 1px #cbc9c8, -22px 44px 1px #cfcdcd, -23px 46px 1px #d4d2d1, -24px 48px 1px #d8d6d5,
    -25px 50px 1px #dbdad9, -26px 52px 1px #dfdddc, -27px 54px 1px #e2e0df, -28px 56px 1px #e4e3e2;
}
.home-block-wrapper p {
  font-family: "Avant Garde", "Avantgarde", "Century Gothic", "CenturyGothic", "AppleGothic", sans-serif;
  font-size: 20px;
  text-rendering: optimizeLegibility;
  color: #393939;
  text-align: center;
}
.news-article-splice {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 25px;
  gap: 25px;
  margin-top: 40px;
  margin-bottom: 40px;
}
.btn-show-more {
  display: flex;
  justify-content: center;
}
.folded-news-for-link {
  margin-top: 25px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 25px;
  gap: 25px;
  margin-bottom: 26px;
}
.header-block-for-news a {
  text-decoration: none;
}
.folded-news-for-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.btn-pagination-on-news-page {
  display: flex;
  justify-content: space-between;
}
.btn-pagination-on-news-page-center {
  display: flex;
  justify-content: center;
}
.not-found-text {
  font-family: "Avant Garde", "Avantgarde", "Century Gothic", "CenturyGothic", "AppleGothic", sans-serif;
  font-size: 30px;
  text-rendering: optimizeLegibility;
  color: #272626;
  text-align: center;
  font-weight: bold;
}
/* Button scroll-up */
.fas .fa-angle-up::before {
  content: "\f106";
}
.scroll-up {
  position: fixed;
  bottom: 28px;
  right: 28px;
  z-index: 3;
}
.scroll-up a {
  height: 42px;
  width: 42px;
  text-align: center;
  background: #151414;
  display: block;
  border-radius: 2px;
}
.scroll-up a span {
  color: #f7f0f0;
  font-size: 30px;
  line-height: 42px;
  cursor: pointer;
}
/* Not Found  */
.main-block-wrapper p {
  border: 4px double rgba(255, 255, 255, 0.25);
  border-width: 4px 0;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 56%;
  left: 50%;
  width: 40em;
  margin: 0 0 0 -20em;
}
.main-block-wrapper span {
  text-transform: uppercase;
  font: 700 4em/1 "Oswald", sans-serif;
  padding: 0.25em 0 0.325em;
  display: block;
  text-shadow: 0 0 80px rgba(255, 255, 255, 0.9);
  animation: bounce-top 2s infinite both;
}
@keyframes bounce-top {
  0% {
    transform: translateY(-45px);
    animation-timing-function: ease-in;
    opacity: 1;
  }
  24% {
    opacity: 1;
  }
  40% {
    transform: translateY(-24px);
    animation-timing-function: ease-in;
  }
  65% {
    transform: translateY(-12px);
    animation-timing-function: ease-in;
  }
  82% {
    transform: translateY(-6px);
    animation-timing-function: ease-in;
  }
  93% {
    transform: translateY(-4px);
    animation-timing-function: ease-in;
  }
  25%,
  55%,
  75%,
  87% {
    transform: translateY(0px);
    animation-timing-function: ease-out;
  }
  100% {
    transform: translateY(0px);
    animation-timing-function: ease-out;
    opacity: 1;
  }
}
@media (max-width: 750px) {
  .app {
    max-width: 100%;
    margin: 10px 15px;
  }
  .logo-wrapper {
    margin-bottom: 19px;
  }
  .logo-wrapper img {
    width: 190px;
    height: 40px;
  }
  .home-block-wrapper h1 {
    margin-bottom: 40px;
    font-size: 45px;
    letter-spacing: 0.15em;
    text-shadow: 1px -1px 0 #767676, -1px 2px 1px #737272, -2px 4px 1px #767474, -3px 6px 1px #787777,
      -4px 8px 1px #7b7a7a, -5px 10px 1px #7f7d7d, -6px 12px 1px #828181, -7px 14px 1px #868585, -8px 16px 1px #8b8a89,
      -9px 18px 1px #8f8e8d, -10px 20px 1px #949392, -11px 22px 1px #999897, -12px 24px 1px #9e9c9c,
      -13px 26px 1px #a3a1a1, -14px 28px 1px #a8a6a6, -15px 30px 1px #adabab, -16px 32px 1px #b2b1b0;
  }
  .home-block-wrapper p {
    font-size: 15px;
  }
  .news-article-splice {
    justify-items: center;
    margin-bottom: 10px;
  }
  .folded-news-for-link {
    margin-bottom: 16px;
    justify-items: center;
  }
  .main-block-wrapper span {
    font: 200 2em/1 "Oswald", sans-serif;
  }
  .main-block-wrapper p {
    padding: 260px 0px;
  }
  .not-found-text {
    font-size: 23px;
    margin-bottom: 25px;
  }
}
